@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap');

$borderColor: #144fc6;
$fillColor: #6ab3f8;

.body {
    background-color: #3494e4;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    padding: 8px;
    height: 100vh;

    h1 {
        margin: 10px 0 0;
    }

    h3 {
        font-weight: 400;
        margin: 10px 0;
    }
}

.cup {
    background-color: #fff;
    border: 4px solid $borderColor;
    color: $borderColor;
    border-radius: 0 0 40px 40px;
    height: 330px;
    width: 150px;
    margin: 30px 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.cupSmall {
    height: 95px;
    width: 50px;
    border-radius: 0 0 15px 15px;
    background-color: rgba($color: #fff, $alpha: 0.9);
    cursor: pointer;
    font-size: 14px;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 5px;
    transition: 0.3s ease;
}

.full {
    background-color: $fillColor;
    color: #fff;
}

.cups {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 280px;
}

.remained {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1;
    transition: 0.3s ease;

    span {
        font-size: 20px;
        font-weight: bold;
    }

    small {
        font-size: 12px;
    }
}

.percentage {
    background-color: $fillColor;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 30px;
    height: 0;
    transition: 0.3s ease;
}

.text {
    text-align: center;
    margin: 0 0 5px;
}